import Rebase from 're-base';
import firebase from 'firebase';

/* Putting API Key on Client application is not safe but in this case,
   we can use Firebase rules to compensate this
*/
const app = firebase.initializeApp({
  apiKey: "AIzaSyAuH9oQl3x9gR_ldBnLOXRdQFDv9QChOD8",
  authDomain: "catch-of-the-day-7a320.firebaseapp.com",
  databaseURL: "https://catch-of-the-day-7a320.firebaseio.com"
});

// Connection to Firebase Realtime database
const base = Rebase.createClass(app.database());

// Now we export it so any files is ready to use the database connection
export { app, base };


