import React from 'react';
import PropTypes from 'prop-types';
import { formatPrice } from '../helpers';

class Fish extends React.Component {
  render() {
    const { details, index } = this.props; // const details = this.props.details
    const isAvailable = details.status === 'available';
    const buttonText = isAvailable ? 'Add To Order' : 'Sold Out!';

    return (
      <li className="menu-fish">
        <img src={details.image} alt={details.name} />
        <h3 className="fish-name">
          {details.name}
          <span className="price">{formatPrice(details.price)}</span>
        </h3>
        <p>{details.desc}</p>
        {/* this.props.addToOrder('fish-1') Instead, this will run on page load
            this.props.addToOrder.bind(null, 'fish-1') One correct way to do it
            or Use arrow functions like demonstrated below
        
            'index' is equivalent to this.props.index which is assigned to
            variable in Line 6, which we pass from the App component.
        */}
        <button onClick={() => this.props.addToOrder(index)} 
                disabled={!isAvailable}>{buttonText}</button>
      </li>
    );
  }
}

Fish.propTypes = {
  details: PropTypes.object.isRequired,
  index: PropTypes.string.isRequired,
  addToOrder: PropTypes.func.isRequired
};

export default Fish;


