import React from 'react';
import PropTypes from 'prop-types';
import { getFunName } from '../helpers';

class StorePicker extends React.Component {

  static contextTypes = {
    router: PropTypes.func
  };

  /* We need to bind goToStore to itself so other functions can use it 
     This is suitable if the function will be called multiple times
     because of code duplication.
  */

  // constructor() {
  //   super();
  //   this.goToStore = this.goToStore.bind(this);
  // }

  goToStore(event) {
    event.preventDefault();
    console.log('You changed the URL');
    // (1) Grab the text from the box
    // We can call store input because we have added the refs below
    const storeId = this.storeInput.value;
    console.log(`Going to ${storeId}`);
    // (2) we're going to transition from / to /store/:storeid
    this.context.router.history.push(`/store/${storeId}`);
  }
  
  // You can also bind the function at onSubmit = {this.goToStore.bind(this)}
  // or Use arrow functions and pass the event to goToStore function

  render() {
    return (
      <form className="store-selector" onSubmit={(e) => this.goToStore(e)}>
        <h2>Please Enter A Store</h2>
        <input type="text" 
               required placeholder="Store Name"
               defaultValue={getFunName()} 
               ref={(input) => {this.storeInput = input}} />
        <button type="submit">Visit store →</button>
      </form>
    );
  }
}

// You can also put static property of context types separately
// StorePicker.contextTypes = {
//   router: PropTypes.func
// };

export default StorePicker;


