import React from 'react';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import PropTypes from 'prop-types';
import { formatPrice } from '../helpers';

class Order extends React.Component {
  constructor() {
    super();
    this.renderOrder = this.renderOrder.bind(this);
  }

  renderOrder(key) {
    const fish = this.props.fishes[key];
    const count = this.props.order[key];
    const removeButton = <button onClick={() => this.props.removeFromOrder(key)}>&times;</button>;

    if (!fish || fish.status === 'unavailable') {
      return (
          <li key={key}>
            Sorry, {fish ? fish.name : 'fish'} is no longer available!
            {removeButton}
          </li>
      );
    }

    return (
      <CSSTransition key={key}
                     classNames="order"
                     timeout={{ enter: 500, exit: 300 }}>
        <li key={key}>
          <span>
            <CSSTransition key={count}
                           classNames="count"
                           timeout={250}>
              {/* React needs a key so it knows who's entering/exitting */}
              <span key={count}>{count}</span>
            </CSSTransition>
            lbs {fish.name} {removeButton}
          </span>
          <span className="price">{formatPrice(count * fish.price)}</span>
        </li>
      </CSSTransition>
    );
  }

  render() {
    const orderIds = Object.keys(this.props.order);
    /* We need to call reduce on orderIds Object. Reduce: Loops over an array 
       and add bunch of stuff or return a new object/array etc
    */
    const total = orderIds.reduce((prevTotal, key) => {
      const fish = this.props.fishes[key];
      const count =  this.props.order[key];
      const isAvailable = fish && fish.status === 'available';
      if (isAvailable) {
        /* We use || (OR) 0 because sometimes the fish can be inside order but
           it can be deleted
        */
        return prevTotal + (count * fish.price || 0);
      }
      return prevTotal;
    }, 0); // 0 is reduce starting value
    
    return (
      <div className="order-wrap">
        <h2>Your Order</h2>
        <ul className="order">
          <TransitionGroup>
            {orderIds.map(this.renderOrder)}
          </TransitionGroup>
          <li className="total">
            <strong>Total:</strong>
            {formatPrice(total)}
          </li>
        </ul>
      </div>
    );
  }
 }

 Order.propTypes = {
   fishes: PropTypes.object.isRequired,
   order: PropTypes.object.isRequired,
   removeFromOrder: PropTypes.func.isRequired
 };

 export default Order;


