import React from 'react';
import PropTypes from 'prop-types';
import { base } from '../base';
import Header from './Header';
import Order from './Order';
import Inventory from './Inventory';
import Fish from './Fish';
import sampleFishes from '../sample-fishes';

class App extends React.Component {
  constructor() {
    super();

    // We need to bind the method so it is accessible
    this.addFish = this.addFish.bind(this);
    this.updateFish = this.updateFish.bind(this);
    this.removeFish = this.removeFish.bind(this);
    this.loadSamples = this.loadSamples.bind(this);
    this.addToOrder = this.addToOrder.bind(this);
    this.removeFromOrder = this.removeFromOrder.bind(this);

    // Initial State (getInitialState)
    this.state = {
      fishes: {},
      order: {}
    };
  }

  componentWillMount() {
    /* Syncing state with Firebase Realtime Database
       This runs right before the <App> is rendered</App>
    */
    this.ref = base.syncState(`${this.props.match.params.storeId}/fishes`, {
      context: this,
      state: 'fishes'
    });

    // Check if there is any order in localStorage
    const localStorageRef = localStorage.getItem(`order-${this.props.match.params.storeId}`);
    
    if (localStorageRef) {
      // update our App component's order state
      this.setState({
        order: JSON.parse(localStorageRef) // Turn string back to Object
      });
    }
  
  }

  componentWillUnmount() {
    base.removeBinding(this.ref);
  }

  // Invoke everytime new props/state is received
  componentWillUpdate(nextProps, nextState) {
    localStorage.setItem(`order-${this.props.match.params.storeId}`,
      JSON.stringify(nextState.order)); // Need to Store the object as String
  }

  addFish(fish) {
    /* Update our state
       ... will take every item from our object and spread it into this object
    */
    const fishes = {...this.state.fishes};

    // Add in our new fish
    const timestamp = Date.now();
    fishes[`fish-${timestamp}`] = fish;
    
    // Set state
    this.setState({ fishes }); // equivalent to { fishes: fishes }

  }

  updateFish(key, updatedFish) {
    const fishes = {...this.state.fishes};
    fishes[key] = updatedFish;
    this.setState({ fishes });
  }

  removeFish(key) {
    const fishes = {...this.state.fishes}
    // Doesn't work with Firebase: delete fishes[key];
    fishes[key] = null;
    this.setState({ fishes })
  }

  loadSamples() {
    this.setState({
      fishes: sampleFishes
    });
  }

  addToOrder(key) {
    // Take a copy of our state using object spread (...)
    const order = {...this.state.order}
    // Update or add the new number of fish ordered
    // We use OR operator, so it adds only one to items we haven't ordered yet
    order[key] = order[key] + 1 || 1
    // Update our state
    this.setState({ order }); // equivalent to { order: order }
  }

  removeFromOrder(key) {
    const order = {...this.state.order};
    delete order[key]; // We can use delete since it's local storage only
    this.setState({ order });
  }

  /* Loop the fishes by mapping them into Array of Object
     We also need to pass the fish component the unique key and the props
  */

  render() {
    return (
      <div className="catch-of-the-day">
        <div className="menu">
          <Header tagLine="Fresh Seafood Market" />
          <ul className="list-of-fishes">
            { /* The 'key' attribute and the 'index' attribute are the exact 
                 same thing. Only React can access 'key', therefore it is not
                 available in other component we pass this into. That is why
                 we need to pass a prop which is accessible from Fish component.
              */
              Object.keys(this.state.fishes)
                    .map(key => <Fish key={key}
                                      index={key}
                                      details={this.state.fishes[key]} 
                                      addToOrder={this.addToOrder} />)
            }
          </ul>
        </div>
        {/* Params are available on <App> level not <Order> level,
            So we need to pass the params props to Order
        */}
        <Order fishes={this.state.fishes}
               order={this.state.order}
               params={this.props.match.params} 
               removeFromOrder={this.removeFromOrder} />
        <Inventory addFish={this.addFish} 
                   removeFish={this.removeFish} 
                   loadSamples={this.loadSamples} 
                   fishes={this.state.fishes} 
                   updateFish={this.updateFish}
                   storeId={this.props.match.params.storeId} />
      </div>
    );
  }
}

App.propTypes = {
  match: PropTypes.object.isRequired
}

export default App;


